import { useTranslation } from "react-i18next";

import { Input, Dropdown } from 'semantic-ui-react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as filterActions from '../assets/store/actions/filter';

const languagesLarge = [
  { key: 'ja', text: ' / Japanese', value: 'japanese', image: { avatar: true, src: require('../assets/images/flags/ja-Japanese.png') }, },
  { key: 'en', text: ' / English', value: 'english', image: { avatar: true, src: require('../assets/images/flags/en-English.png') }, },
  { key: 'pr', text: ' / Persian', value: 'persian', image: { avatar: true, src: require('../assets/images/flags/ir-Iran.png') }, },
  { key: 'pl', text: ' / Polish', value: 'polish', image: { avatar: true, src: require('../assets/images/flags/pl-Polish.png') }, },
  { key: 'tk', text: ' / Turkish', value: 'turkish', image: { avatar: true, src: require('../assets/images/flags/tk-Turkish.png') }, },
  { key: 'de', text: ' / German', value: 'german', image: { avatar: true, src: require('../assets/images/flags/de-German.png') }, },
  { key: 'ar', text: ' / Arabic', value: 'arabic', image: { avatar: true, src: require('../assets/images/flags/ar-Arabic.png') }, },
  { key: 'he', text: ' / Hebrew', value: 'hebrew', image: { avatar: true, src: require('../assets/images/flags/he-hebrew.png') }, },
  { key: 'es', text: ' / Spanish', value: 'spanish', image: { avatar: true, src: require('../assets/images/flags/es-Spanish.png') }, },
  { key: 'fr', text: ' / French', value: 'french', image: { avatar: true, src: require('../assets/images/flags/fr-French.png') }, },
  { key: 'ch', text: ' / Chinese', value: 'chinese', image: { avatar: true, src: require('../assets/images/flags/ch-Chinese.png') }, },
  { key: 'it', text: ' / Italian', value: 'italian', image: { avatar: true, src: require('../assets/images/flags/it-Italian.png') }, },
]

const citiesSmall = [
  { key: 'mn', text: ' / Minsk', value: 'minsk', },
  { key: 'vt', text: ' / Vitebsk', value: 'vitebsk', }
]

const languagesSmall =  [
  { key: 'ru', text: ' / Russian', value: 'russian', image: { avatar: true, src: require('../assets/images/flags/ru-Russian.png') }, },
  { key: 'by', text: ' / Belarusian', value: 'belarusian', image: { avatar: true, src: require('../assets/images/flags/by-Belarusian.png') }, },
]

const citiesLarge = [
  { key: 'bn', text: ' / Baranovichi', value: 'baranovichi', },
  { key: 'bo', text: ' / Borisov', value: 'borisov', },
  { key: 'bv', text: ' / Braslav', value: 'braslav', },
  { key: 'br', text: ' / Brest', value: 'brest', },
  { key: 'vt', text: ' / Vitebsk', value: 'vitebsk', },
  { key: 'gl', text: ' / Glubokoye', value: 'glubokoye', },
  { key: 'gm', text: ' / Gomel', value: 'gomel', },
  { key: 'gr', text: ' / Grodno', value: 'grodno', },
  { key: 'dv', text: ' / Dyatlovo', value: 'dyatlovo', },
  { key: 'kr', text: ' / Korelichi', value: 'korelichi', },
  { key: 'lg', text: ' / Logoisk', value: 'logoisk', },
  { key: 'mn', text: ' / Minsk', value: 'minsk', },
  { key: 'mg', text: ' / Mogilev', value: 'mogilev', },
  { key: 'ml', text: ' / Molodechno', value: 'molodechno', },
  { key: 'ng', text: ' / Novogrudok', value: 'novogrudok', },
  { key: 'pn', text: ' / Pinsk', value: 'pinsk', },
  { key: 'pl', text: ' / Polotsk', value: 'polotsk', },
  { key: 'pr', text: ' / Pruzhany', value: 'pruzhany', },
]

const mapStateToProps = ({ filter }) => ({
  filterLang: filter.filterLang,
  filterCity: filter.filterCity
});

const mapDispatchToProps = dispatch => ({
  ...bindActionCreators(filterActions, dispatch)
});

const FilterBlock = (filterProps) => {
  const { t } = useTranslation();
  const {searchQuery, setSearchQuery, setFilterLang, setFilterCity, type} = filterProps;
  return (
    <div className="filter-container">
      <Dropdown placeholder='' clearable fluid multiple selection options={type==='guides' ? languagesSmall :languagesLarge} onChange={(e, { value }) => setFilterLang({ value })} />
      <Dropdown placeholder='' clearable fluid multiple selection options={type==='guides' ? citiesSmall : citiesLarge} onChange={(e, { value }) => setFilterCity({ value })} />
      <Input
        icon="search"
        onChange={e => setSearchQuery(e.target.value)}
        value={searchQuery} placeholder={t('search')}></Input>
    </div>
  )
};

export default connect(mapStateToProps, mapDispatchToProps)(FilterBlock);






